，天真烂漫。单纯的悟空在初期冒险篇章中各种笑料也让大家捧腹不已。 结局时，悟空尽管已经步入中年，成为爷爷辈的人，不过开朗乐观的性格依然没有变。他还是那个成天乐呵呵，一心修炼期望遇到更强大对手的可靠战士。")])])])]),_c('el-card',{staticClass:"box-card"},[_c('div',{staticClass:"recent-posts"},[_c('div',{staticClass:"right"},[_c('span',[_vm._v("《龙珠》是龙珠系列的发端，记录了龙珠主角悟空的成长。悟空和布尔玛为了集齐七颗龙珠游历世界，进行冒险。在这期间，悟空结识了许多要好的朋友，也打败了许多想利用龙珠作恶的邪恶组织。《龙珠》最打动我的一点，就是随着悟空的冒险，一点一点拓宽自己的见识，逐渐了解了这个多样的世界。 每每回顾，都会联想到自己的成长历程，身为农村孩子的自己第一次出村子、到镇上、到市中心，经历这一次次的”冒险“，接受了大量的经验，进而好奇心得到了满足。这种”雏鸟初见世界之大“的震撼感，通过回顾《龙珠》自己都能很清楚的回想起来。 《龙珠》的一条主线如果是收集龙珠的话，那么第二条主线就是天下第一武道会。第21届武道会决赛由龟仙人假扮的陈龙对战悟空，第22届天津饭对战悟空，第23届短笛（比克）对战悟空，这三战见证了悟空实力上的成长，也见证了悟空性格上的成熟转变。")])]),_c('div',{staticClass:"left"},[_c('img',{staticStyle:{"border-radius":"10px"},attrs:{"src":require("../../assets/images/bizhi/4.webp"),"alt":""}})])])]),_c('el-card',{staticClass:"box-card"},[_c('div',{staticClass:"recent-posts"},[_c('div',{staticStyle:{"width":"100%"}},[_c('img',{staticStyle:{"border-radius":"10px"},attrs:{"src":require("../../assets/images/bizhi/8.webp"),"alt":""}})])])]),_c('el-card',{staticClass:"box-card"},[_c('div',{staticClass:"recent-posts"},[_c('div',{staticClass:"left"},[_c('img',{staticStyle:{"border-radius":"10px"},attrs:{"src":require("../../assets/images/bizhi/11.webp"),"alt":""}})]),_c('div',{staticClass:"right"},[_c('span',[_vm._v("故事简介：拥有财富、名声、权力，这世界上的一切的男人 “海贼王”哥尔·D·罗杰，在被行刑受死之前说了一句话，让全世界的人都涌向了大海。“想要我的宝藏吗？如果想要的话，那就到海上去找吧，我全部都放在那里。”，世界开始迎接“大海贼时代”的来临 。 时值“大海贼时代”，为了寻找传说中海贼王罗杰所留下的大秘宝“ONE PIECE”，无数海贼扬起旗帜，互相争斗。有一个梦想成为海贼王的少年叫路飞，他因误食“恶魔果实”而成为了橡皮人，在获得超人能力的同时付出了一辈子无法游泳的代价。十年后，路飞为实现与因救他而断臂的香克斯的约定而出海，他在旅途中不断寻找志同道合的伙伴，开始了以成为海贼王为目标的冒险旅程 。")])])])]),_c('el-card',{staticClass:"box-card"},[_c('div',{staticClass:"recent-posts"},[_c('div',{staticClass:"right"},[_c('span',[_vm._v("蒙奇·D·路飞 “草帽一伙”的船长，外号“草帽小子”。东海出身，悬赏金30亿贝里。“草帽一伙”的创立者，是被称作“极恶的世代”中登陆香波地群岛的11位超新星的其中一位。在“和之国”事件中将原“四皇”之一的“百兽”凯多击溃，顶替其成为新的“四皇”。是食用了橡胶果实的橡胶人（五老星认为这是人人果实·幻兽种·尼卡形态）。梦想成为“海贼王”，以此为目标在大海上航行。")])]),_c('div',{staticClass:"left"},[_c('img',{staticStyle:{"border-radius":"10px"},attrs:{"sr